import { useState, useEffect, FC } from 'react';
import { IWeatherDay } from './types.js';
import Today from '../components/Today';
import Longterm from '../components/Longterm';
import Header from '../components/Header';
import { useDispatch } from 'react-redux';
import { changeTimezoneAction } from '../store/weatherReducer';
import { useTypedSelector } from '../hooks/useTypedSelector';

const MainPage: FC = () => {

   const dispath = useDispatch()

   const [theme, setTheme] = useState('')
   const [loader, setLoader] = useState(false)
   const [weatherDays, setWeatherDays] = useState<IWeatherDay[]>([])

   const stateRedux = useTypedSelector(state => state)

   // указываем что ключом может быть строка
   const coordinatesCities: { [key: string]: any } = {
      minsk: {
         lat: '53.90454',
         lon: '27.56152'
      },
      moscow: {
         lat: '55.7522',
         lon: '27.56152'
      },
      london: {
         lat: '51.5085',
         lon: '-0.12574'
      }
   }

   // получаем данные погоды
   useEffect(() => {

      const currentCityLat = coordinatesCities[stateRedux.city].lat
      const currentCityLon = coordinatesCities[stateRedux.city].lon

      setLoader(false)

      fetch(`http://api.openweathermap.org/data/2.5/onecall?lat=${currentCityLat}&lon=${currentCityLon}&lang=ru&exclude=minutely,hourly,alerts&units=metric&appid=76b3e464497c7c3ae109db97b9bf67d0`)
         .then(data => data.json())
         .then(data => {
            dispath(changeTimezoneAction(data.timezone))
            setWeatherDays([...data.daily])
            setLoader(true)
         })
         .catch(e => console.log(e))
   }, [stateRedux.city])

   return (
      <div className={`wrapper ${theme}`}>
         <Header theme={theme} setTheme={setTheme} />

         <div className="main">
            {!loader
               ?
               <div className="loader">
                  <img src="./React-Weather/images/sun.svg" alt="sun" className='loader_img' />
               </div>
               :
               <>
                  <Today todayWeather={weatherDays[0]} />

                  <Longterm allWeather={weatherDays} loader={loader} setLoader={setLoader} />
               </>
            }
         </div>
      </div >
   );
}

export default MainPage;