import { FC } from 'react';
import MySelect from '../components/UI/MySelect';

interface ITheme {
   theme: string;
   setTheme: (state: ((state: string) => string)) => void;
}

const Header: FC<ITheme> = ({ theme, setTheme }) => {

   const changeTheme = () => {
      setTheme((prev: string) => {
         // проверяем какой сейчас стиль установлен на странице и возвращаем нужное значение
         return prev === '' ? '_dark' : ''
      })
   }

   return (
      <div className="header">
         <div className="header__container">
            <div className="header__logo">
               <img src="./React-Weather/images/header-logo.png" className="header__logo-img" alt="weather-logo"></img>
               <span className="header__logo-txt">React weather</span>
            </div>
            <div className="header__actions">
               <div onClick={() => changeTheme()} className="header__theme">
                  <img src="./React-Weather/images/theme.png" className="header__theme-img" alt="theme"></img>
               </div>
               <div className="header__sort">
                  <MySelect />
               </div>
            </div>
         </div>
      </div>
   );
}

export default Header;