import { FC, useState } from "react";
import { IWeatherDay } from "../pages/types";
import LongtermItem from "./LongtermItem";

interface ILongtermProps {
   allWeather: IWeatherDay[];
   loader: boolean;
   setLoader: (loader: boolean) => void;
}

const Longterm: FC<ILongtermProps> = ({ allWeather, setLoader }) => {

   const [countDay, setCountDay] = useState(3)

   return (
      <section className="longterm">
         <div className="longterm__container">
            <div className="longterm__actions">
               <div className="longterm__tabs">
                  <ul className="longterm__tabs-list">
                     <li onClick={() => setCountDay(3)} className="longterm__tabs-item">Три дня</li>
                     <li onClick={() => setCountDay(5)} className="longterm__tabs-item">Пять дней</li>
                     <li onClick={() => setCountDay(7)} className="longterm__tabs-item">На неделю</li>
                  </ul>
               </div>
               <button onClick={() => setLoader(false)} className="longterm__cancel">Отменить</button>
            </div>
            <div className="longterm__days">
               {allWeather.map((day, index) => {
                  if (index < countDay) {
                     return (
                        <LongtermItem dayInfo={day} key={index} index={index} />
                     )
                  }
               })

               }
            </div>
         </div>
      </section>
   );
}

export default Longterm;