
const weatherState = {
   city: 'minsk',
   timezone: 'Europe/Minsk',
}

interface IActionChangeCity {
   type: string;
   payload: string;
}

interface IActionChangeTimezone {
   type: string;
   payload: string;
}

export interface IWeatherReducer {
   city: string;
   timezone: string;
}

export const weatherReducer = (state = weatherState, action: IActionChangeCity | IActionChangeTimezone): IWeatherReducer => {
   switch (action.type) {
      case 'CHANGE_CITY':
         return { ...state, city: action.payload }
      case 'CHANGE_TIMEZONE':
         return { ...state, timezone: action.payload }
      default:
         return state
   }
}

// экшены этого редьюсера
export const changeCityAction = (payload: string) => ({ type: "CHANGE_CITY", payload })
export const changeTimezoneAction = (payload: string) => ({ type: "CHANGE_TIMEZONE", payload })