import { FC } from "react";
import { IWeatherDay } from "../pages/types";

interface IDayInfo {
   dayInfo: IWeatherDay;
   index: number;
}

const LongtermItem: FC<IDayInfo> = ({ dayInfo, index }) => {

   //достаем из полученной даты в api нужные параметры
   let dayWeek = new Date(dayInfo.dt * 1000).toLocaleString('ru', { weekday: 'short', });
   dayWeek = dayWeek.charAt(0).toUpperCase() + dayWeek.slice(1)

   let dayDate = new Date(dayInfo.dt * 1000).toLocaleString('ru', { day: 'numeric', month: 'short', });

   //обработанные для нужного формата данные из api
   const parsedTemp = {
      tempDay: Math.round(dayInfo?.temp.day),
      tempNight: Math.round(dayInfo?.temp.night),
      description: dayInfo?.weather[0].description.charAt(0).toUpperCase() + dayInfo?.weather[0].description.slice(1),
   }

   return (
      <div className="longterm__item longterm-item">
         <div className="longterm-item__date">
            <p className="longterm-item__date-day">{index === 0 ? 'Сегодня' : dayWeek}</p>
            <p className="longterm-item__date-number">{dayDate}</p>
         </div>

         <div className="longterm-item__icon-container">
            {/* <img src="./images/sun.svg" className="longterm-item__icon" alt="weather"></img> */}
            <img src={`http://openweathermap.org/img/w/${dayInfo.weather[0].icon}.png`} className="longterm-item__icon" alt="weather"></img>
         </div>

         <div className="longterm-item__degrees">
            <p className="longterm-item__degrees-day">{parsedTemp.tempDay}°</p>
            <p className="longterm-item__degrees-night">{parsedTemp.tempNight}°</p>
            <p className="longterm-item__degrees-weather">{parsedTemp.description}</p>
         </div>
      </div>
   );
}

export default LongtermItem;