import { FC, useEffect } from "react";
import { IWeatherDay } from '../pages/types';
import Moment from 'react-moment';
import 'moment-timezone';
import { useTypedSelector } from '../hooks/useTypedSelector';

interface ITodayProps {
   todayWeather: IWeatherDay;
}

const Today: FC<ITodayProps> = ({ todayWeather }) => {

   const stateRedux = useTypedSelector(state => state)

   //обработанные для нужного формата данные из api
   const parsedWeather = {
      feelsLike: Math.round(todayWeather?.feels_like.day),
      pressure: Math.round(todayWeather?.pressure / 1.333),
      tempDay: Math.round(todayWeather?.temp.day),
      tempNight: Math.round(todayWeather?.temp.night),
      description: todayWeather?.weather[0].description.charAt(0).toUpperCase() + todayWeather?.weather[0].description.slice(1),
      icon: todayWeather?.weather[0].icon,
      main: todayWeather?.weather[0].main,
      windSpeed: Math.round(todayWeather?.wind_speed),
      cityName: stateRedux.city.charAt(0).toUpperCase() + stateRedux.city.slice(1),
   }

   return (
      <section className="today">
         <div className="today__container">
            <div className="today__weather today-weather">
               <div className="today-weather__top">
                  <div className="today-weather__text">
                     <p className="today-weather__degrees">{parsedWeather.tempDay}°</p>
                     <p className="today-weather__date">Сегодня</p>
                  </div>
                  <div className="today-weather__icon-container">
                     {/* <img src="./images/sun.svg" className="today-weather__icon" alt="sun"></img> */}
                     <img src={`http://openweathermap.org/img/w/${parsedWeather.icon}.png`} className="today-weather__icon" alt="weather-logo"></img>
                  </div>
               </div>
               <div className="today-weather__bottom">
                  <p className="today-weather__time">Время: <Moment format="HH.mm" interval={1000} tz={stateRedux.timezone}></Moment></p>
                  <p className="today-weather__city">Город: {parsedWeather.cityName}</p>
               </div>
            </div>

            <div className="today__parameters today-parameters">
               <ul className="today-parameters__list">
                  <li className="today-parameters__item">
                     <div className="today-parameters__item-container">
                        <img src="./React-Weather/images/degr.png" className="today-parameters__item-img" alt="parametr" />
                     </div>
                     <p className="today-parameter__item-parameter">Температура</p>
                     <p className="today-parameter__item-description">{parsedWeather.tempDay}° - ощущается как {parsedWeather.feelsLike}°</p>
                  </li>

                  <li className="today-parameters__item">
                     <div className="today-parameters__item-container">
                        <img src="./React-Weather/images/pressure.png" className="today-parameters__item-img" alt="parametr" />
                     </div>
                     <p className="today-parameter__item-parameter">Давление</p>
                     <p className="today-parameter__item-description">{parsedWeather.pressure} мм ртутного столба - нормальное</p>
                  </li>

                  <li className="today-parameters__item">
                     <div className="today-parameters__item-container">
                        <img src="./React-Weather/images/up.png" className="today-parameters__item-img" alt="parametr" />
                     </div>
                     <p className="today-parameter__item-parameter">Осадки</p>
                     <p className="today-parameter__item-description">{parsedWeather.main === 'Rain' || parsedWeather.main === 'Snow' ? parsedWeather.description : 'Без осадков'}</p>
                  </li>

                  <li className="today-parameters__item">
                     <div className="today-parameters__item-container">
                        <img src="./React-Weather/images/wind.png" className="today-parameters__item-img" alt="parametr" />
                     </div>
                     <p className="today-parameter__item-parameter">Ветер</p>
                     <p className="today-parameter__item-description">{parsedWeather.windSpeed} м/с - {parsedWeather.windSpeed <= 5 ? 'слабый ветер' : 'сильный ветер'}</p>
                  </li>
               </ul>
            </div>
         </div>
      </section>
   );
}

export default Today;